@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

html,body{font-family:"Noto Sans KR",sans-serif;background-color:#f5f5f5;font-size:13px}html,body,#root{height:100%}#root{max-width:1024px;margin:auto;display:flex;flex-direction:column}#root>.tab-page{flex:1 1;overflow:auto}.text-align-center{text-align:center !important}.orange-bg{background-color:#fbc02d !important}.green-bg{background-color:#43a047 !important}.page{display:flex;flex-direction:column;height:100%}.page .table-wrapper{flex:1 1;overflow:auto}.page .table-wrapper .number-input{margin:0 .7em}.page .buttons{margin:.5em;border-radius:3px;overflow:hidden}.page .buttons button{flex:1 1;border-radius:0;padding:1em}
.number-input{display:flex;flex-direction:row;box-sizing:border-box}
.inventory-page .table-wrapper th:nth-child(1){width:33%}.inventory-page .table-wrapper th:nth-child(2),.inventory-page .table-wrapper th:nth-child(3){min-width:8em}.inventory-page .table-wrapper td:nth-child(1),.inventory-page .table-wrapper th:nth-child(1){padding-left:1em}.inventory-page .table-wrapper td:last-child,.inventory-page .table-wrapper th:last-child{padding-right:1em}#clipboard-modal{position:absolute;top:50%;left:50%;max-width:calc(100% - 2em);max-height:calc(100% - 2em);transform:translate(-50%, -50%);min-width:300px;overflow:auto}
